<template>

<div> 
    <!-- display the data  -->
  <state-summary v-bind:states="states"></state-summary>

  <div class="state-list-container"> 
      <!-- container for one state -->
    <div class="state-container" v-for="state in states" v-bind:key="state.name">
        <state-detail v-bind:state="state" v-on:update-visited="updateVisited"></state-detail>   
    </div>
  </div>
</div>
</template>

<script>
import StateDetail from './StateDetail.vue'
import StateSummary from './StateSummary.vue'

export default {
  components: { 
    StateDetail,

    StateSummary
  },
  name: 'StateList', 
  data() {
      return {
          states: [] // empty array
      }
  },
  mounted() {
      this.fetchAllStates() /// 
  },
  methods: { /// this will 
      fetchAllStates() {
          this.$stateService.getAllStates().then(states => {
              this.states = states
          }).catch(err => {
              alert('Sorry, cannot fetch state list')
              console.error(err)
          }) // this where you get all the states
      },
      updateVisited(stateName, visited) { //
          this.$stateService.setVisited(stateName, visited).then(response => {
              this.fetchAllStates() // if this is good fetchall
          }).catch(err => {
              alert('Sorry, unable to update this state')
              console.error(err)
          })
      }
  }
}
</script>

<style scoped>

.state-list-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

.state-container {
    margin: 1rem;
}
</style>
